import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { localStoragedecryp } from "../../../../components/helpers/function";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { getDataNoTimeOut } from "../../../../config/axios";
import { formatDate, localStorageencryp } from "../../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
import * as moment from "moment-timezone";

const HeadLaporanPenjualanMkc = lazy(() =>
  import("./HeadLaporanPenjualanMkc")
);

class LaporanPenjualanMarketPlace extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanDataPenjualan: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
      marketplace: data.marketplace,
    };
    getDataNoTimeOut("penjualan/report-marketplaces?tgl_from=" + data_post.tgl_awal + "&tgl_to=" + data_post.tgl_akhir+"&marketplace="+data_post.marketplace)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
          });
        } else {
          ToastNotification("success", "Laporan Transaksi Penjualan Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          let databarang = [];
          res.data.forEach(element => {
            let row = {
                no_faktur_jual: element.no_faktur_jual,
                tgl_system: element.tgl_system,
                kode_sales: element.kode_sales,
                kode_member:element.kode_member,
                nama_customer: element.nama_customer,
                no_hp: element.no_hp,
                alamat_customer: element.alamat_customer,
                berat:element.berat,
                berat_awal: element.berat_jual,
                harga_jual: parseInt(element.harga_jual) + parseInt(element.harga_atribut),
                harga_gram: element.harga_gram,
                harga_atribut:element.harga_atribut,
                ongkos: element.ongkos,
                harga_total: element.harga_total,
                kode_barcode :element.kode_barcode,
                kode_barang: element.kode_barang,
                nama_barang:element.nama_barang,
                kode_group: element.kode_group,
                kode_dept: element.kode_dept,
                nama_atribut: element.nama_atribut,
                berat_atribut: element.berat_atribut,
                kode_gudang: element.kode_gudang,
                kode_baki: element.kode_baki,
                kode_marketplace : element.kode_marketplace,
            }
            databarang.push(row)
          });
          this.setState({
            export: true,
            isLoading: false,
            LaporanDataPenjualan: databarang,
            
          });
        }
      })
      .catch((error) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          isLoading: false,
          export: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">
            Laporan Penjualan Marketplace
          </li>
        </ol>
        <h1 className="page-header">Laporan Penjualan Marketplace </h1>
        <Panel>
          <PanelHeader>Laporan Penjualan Marketplace</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadLaporanPenjualanMkc
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanDataPenjualan={this.state.LaporanDataPenjualan}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}
export default LaporanPenjualanMarketPlace;
